body {
  margin: 0;
  padding: 0;
  font: normal 300 100%/1.25 'Source Sans Pro', sans-serif;
  color: #3A405A; }

input, textarea, button {
  font: inherit; }

h1, h2, h3, h4 {
  font: normal 400 100%/1 'Chewy', sans-serif;
  margin: 0; }

h1 {
  font-size: 9em; }

h2 {
  font-size: 3em; }

header {
  background: right bottom/cover url("/img/hero.jpg") no-repeat #4ECDC4;
  color: white;
  height: 60vh;
  text-align: center; }

.start {
  text-align: center;
  padding: 4em 0; }

.leading, header p {
  font-size: 1.25em; }

.container {
  display: table-row; }
  .container-wrapper {
    display: table;
    width: 100%;
    max-width: 50em;
    margin: auto;
    height: 100%;
    padding: 2em 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .container-inner {
    vertical-align: middle;
    display: table-cell;
    padding: 2em;
    position: relative; }

.play {
  position: absolute;
  display: block;
  width: 6em;
  height: 6em;
  bottom: -1em;
  left: -1em;
  border: 0.25em solid #4ECDC4;
  background: rgba(255, 255, 255, 0.75);
  border-radius: 50%;
  color: #4ECDC4;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out; }
  .play:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    background: rgba(255, 255, 255, 0.9); }
  section:nth-child(odd) .play {
    left: auto;
    right: -1em;
    background: rgba(78, 205, 196, 0.75);
    color: #FFFFFF;
    border: 0.25em solid #FFFFFF; }
    section:nth-child(odd) .play:hover {
      background: rgba(78, 205, 196, 0.9); }

section:nth-child(odd) {
  background: #4ECDC4; }

.device {
  height: 50vh;
  float: left;
  -webkit-transform: rotate(2deg) scale(1.2);
      -ms-transform: rotate(2deg) scale(1.2);
          transform: rotate(2deg) scale(1.2); }
  section:nth-child(odd) .device {
    -webkit-transform: rotate(-2deg) scale(1.2);
        -ms-transform: rotate(-2deg) scale(1.2);
            transform: rotate(-2deg) scale(1.2); }

form {
  display: none; }
  form ul {
    list-style: none;
    padding: 0; }
    form ul span {
      display: inline-block;
      padding-left: 1em; }
    form ul label + input {
      margin-left: 1em; }



